  max-height: 80vh;
  overflow: hidden;
  position: relative;
  z-index: 1;
  animation: modalSlideIn 0.3s ease-out;
  transform: scale(1);
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: scale(0.9) translateY(-20px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.modal-header {
  display: flex;
  justify-content: space-between;
