}


.calendar-container.rtl .event-color-bar {
  order: 1;
}

.calendar-container.rtl .event-content {
  order: 0;
}

/* Day Reminders Modal */
.day-reminders-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.day-reminders-modal .modal-content {
  background: white;
  border-radius: 12px;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  overflow-y: auto;
}

.day-reminders-modal .modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  background: #D4AF5F;;
  color: #1B1D21;
  border-radius: 12px 12px 0 0;
}

.day-reminders-modal .modal-header h3 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 600;
  color: #1B1D21;
}

.day-reminders-modal .close-btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
  color: white;
  transition: all 0.2s;
}

.day-reminders-modal .close-btn:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.day-reminders-modal .modal-body {
  padding: 24px;
}

.day-reminders-modal .reminders-list {
  max-height: 50vh;
  overflow-y: auto;
}

.day-reminders-modal .reminder-item {
  display: flex;
  align-items: flex-start;
  padding: 16px;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  margin-bottom: 12px;
  transition: all 0.2s;
  background: #fafafa;
}

.day-reminders-modal .reminder-item:hover {
  border:1px solid #FF3B3B;
  box-shadow: 0 2px 8px rgba(255, 59, 59, 0.1);
}

.day-reminders-modal .reminder-color-bar {
  width: 4px;
  height: 100%;
  border-radius: 2px;
  margin-right: 16px;
  flex-shrink: 0;
}

.day-reminders-modal .reminder-content {
  flex: 1;
}

.day-reminders-modal .reminder-title {
  margin: 0 0 8px 0;
  font-size: 1rem;
  font-weight: 600;
  color: #111827;
}

.day-reminders-modal .reminder-description {
  margin: 0 0 12px 0;
  color: #6b7280;
  font-size: 0.875rem;
  line-height: 1.4;
}

.day-reminders-modal .reminder-dates {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.day-reminders-modal .date-item {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.875rem;
}

.day-reminders-modal .date-label {
  font-weight: 500;
  color: #374151;
  min-width: 120px;
}

.day-reminders-modal .date-value {
  color: #6b7280;
}

.day-reminders-modal .no-reminders {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  text-align: center;
  color: #6b7280;
}

.day-reminders-modal .no-reminders svg {
  margin-bottom: 16px;
  color: #d1d5db;
}

.day-reminders-modal .no-reminders h4 {
  margin: 0 0 8px 0;
  font-size: 1.125rem;
  font-weight: 600;
  color: #374151;
}

.day-reminders-modal .no-reminders p {
  margin: 0;
  font-size: 0.875rem;
  line-height: 1.4;
}

/* RTL Support for Day Reminders Modal */
.rtl .day-reminders-modal .reminder-item {
  flex-direction: row-reverse;
}

.rtl .day-reminders-modal .reminder-color-bar {
  margin-right: 0;
  margin-left: 16px;
}

/* Responsive Reminder Indicator */
@media (max-width: 768px) {
  .reminder-indicator {
    display: none; /* Hide reminder indicator on small screens */
  }
  
  .day-reminders-modal .modal-content {
    width: 95%;
    margin: 20px;
  }
  
  .day-reminders-modal .modal-header,
  .day-reminders-modal .modal-body {
    padding: 16px;
  }
  
  .day-reminders-modal .reminder-item {
    padding: 12px;
  }
  
  .day-reminders-modal .date-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 4px;
  }
  
  .day-reminders-modal .date-label {
    min-width: auto;
  }
}
